<template>
  <div class="register-container">
    <!-- 注册内容 -->
    <div class="register">
      <h3>
        注册新用户
        <span class="go"
          >我有账号，去 <a href="login.html" target="_blank">登陆</a>
        </span>
      </h3>
      <div class="content">
        <label>手机号:</label>
        <input
          type="text"
          placeholder="请输入你的手机号"
          v-model="phone"
          name="phone"
          v-validate="{ required: true, regex: /^1[0-9]{10}$/ }"
          :class="{ error: errors.has('phone') }"
        />
        <span class="error-msg">{{ errors.first('phone') }}</span>
      </div>
      <div class="content">
        <label>验证码:</label>
        <input
          type="text"
          placeholder="请输入验证码"
          name="sendCode"
          v-model="sendCode"
          v-validate="{ required: true, regex: /^[0-9]{6}$/ }"
          :class="{ error: errors.has('sendCode') }"
        />
        <button @click="sendCodeGet" :disabled="isceriy">
          点击获取验证码 <i v-show="isceriy">{{ send }}s</i>
        </button>
        <span class="error-msg">{{ errors.first('sendCode') }}</span>
      </div>
      <div class="content">
        <label>登录密码:</label>
        <input
          type="text"
          placeholder="请输入你的登录密码"
          v-model="password"
          name="password"
          v-validate="{
            required: true,
            regex: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,15}$/,
          }"
          :class="{ error: errors.has('password') }"
        />
        <span class="error-msg">{{ errors.first('password') }}</span>
      </div>
      <div class="content">
        <label>确认密码:</label>
        <input
          type="text"
          placeholder="请输入确认密码"
          v-model="repPassword"
          name="repassword"
          v-validate="{
            required: true,
            is: password,
          }"
          :class="{ error: errors.has('repassword') }"
        />
        <span class="error-msg">{{ errors.first('repassword') }}</span>
      </div>
      <div class="controls">
        <input
          type="checkbox"
          v-model="isAgreen"
          name="Isagree"
          v-validate="{
            Isagree: '',
          }"
        />
        <span>同意协议并注册《尚品汇用户协议》</span>
        <span class="error-msg">{{ errors.first('Isagree') }}</span>
      </div>
      <div class="btn">
        <button @click="registerOk">立即注册</button>
      </div>
    </div>

    <!-- 底部 -->
    <div class="copyright">
      <ul>
        <li>关于我们</li>
        <li>联系我们</li>
        <li>联系客服</li>
        <li>商家入驻</li>
        <li>营销中心</li>
        <li>手机尚品汇</li>
        <li>销售联盟</li>
        <li>尚品汇社区</li>
      </ul>
      <div class="address">地址：北京市昌平区宏福科技园综合楼6层</div>
      <div class="beian">京ICP备19006430号</div>
    </div>
  </div>
</template>

<script>
import { getSendCode, reqUserRegister } from '@/api'
export default {
  name: 'Register',
  data() {
    return {
      sendCode: '',
      phone: '',
      send: 5,
      isceriy: false,
      password: '',
      repPassword: '',
      isAgreen: false,
    }
  },
  methods: {
    async sendCodeGet() {
      try {
        let re = await getSendCode(this.phone)
        this.sendCode = re
        this.isceriy = true
        let timer = setInterval(() => {
          this.send--
          if (this.send <= 0) {
            clearInterval(timer)
            this.send = 5
            this.isceriy = false
          }
        }, 1000)
      } catch (error) {
        console.log(error.message)
      }
    },
    async registerOk() {
      // 群体校验
      let formBol = await this.$validator.validateAll()
      if (!formBol) return this.$message.error('账号格式不正确')

      let { sendCode, phone, password, repPassword, isAgreen } = this
      if (
        !(
          sendCode &&
          phone &&
          password &&
          repPassword &&
          password === repPassword &&
          isAgreen
        )
      )
        return alert('信息不全')

      try {
        await reqUserRegister({ phone, password, code: sendCode })
        alert('注册成功')
      } catch (error) {
        console.log(error)
      }
    },
  },
}
</script>

<style lang="less" scoped>
// 表单错误提示信息
.error {
  border: solid 1px red !important;
}
.register-container {
  .register {
    width: 1200px;
    height: 445px;
    border: 1px solid rgb(223, 223, 223);
    margin: 0 auto;

    h3 {
      background: #ececec;
      margin: 0;
      padding: 6px 15px;
      color: #333;
      border-bottom: 1px solid #dfdfdf;
      font-size: 20.04px;
      line-height: 30.06px;

      span {
        font-size: 14px;
        float: right;

        a {
          color: #e1251b;
        }
      }
    }

    div:nth-of-type(1) {
      margin-top: 40px;
    }

    .content {
      padding-left: 390px;
      margin-bottom: 18px;
      position: relative;

      label {
        font-size: 14px;
        width: 96px;
        text-align: right;
        display: inline-block;
      }

      input {
        width: 270px;
        height: 38px;
        padding-left: 8px;
        box-sizing: border-box;
        margin-left: 5px;
        outline: none;
        border: 1px solid #999;
      }

      img {
        vertical-align: sub;
      }

      .error-msg {
        position: absolute;
        top: 100%;
        left: 495px;
        color: red;
      }
    }

    .controls {
      text-align: center;
      position: relative;

      input {
        vertical-align: middle;
      }

      .error-msg {
        position: absolute;
        top: 100%;
        left: 495px;
        color: red;
      }
    }

    .btn {
      text-align: center;
      line-height: 36px;
      margin: 17px 0 0 55px;

      button {
        outline: none;
        width: 270px;
        height: 36px;
        background: #e1251b;
        color: #fff !important;
        display: inline-block;
        font-size: 16px;
      }
    }
  }

  .copyright {
    width: 1200px;
    margin: 0 auto;
    text-align: center;
    line-height: 24px;

    ul {
      li {
        display: inline-block;
        border-right: 1px solid #e4e4e4;
        padding: 0 20px;
        margin: 15px 0;
      }
    }
  }
}
</style>
